:root {
  --blue: #558FDC;
  --white: #F9F8F8;
}

html {
  scroll-behavior: smooth;
}
a{
  text-decoration: none;
  color: #558FDC;
  color: var(--blue);
}

.App {
  text-align: center;
}

body{
  font-family: 'Nunito Sans', sans-serif;
  background-color: #F9F8F8;
  background-color: var(--white);
  font-size: 17px;
}

#SNSBar{
  position: fixed;
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  height:200px;
  bottom:10%;
  -webkit-animation: snsbardrop ease 3s;
          animation: snsbardrop ease 3s;
  left:0px;
}
/* #SNSBar{
  display: flex;
  flex-direction: row;
  margin: 20px;
} */
@-webkit-keyframes snsbardrop{
  0% {
    left:-60px;
  }
  75%{
    left:-60px;
  }
  100% {
    left:0px;
  }
}
@keyframes snsbardrop{
  0% {
    left:-60px;
  }
  75%{
    left:-60px;
  }
  100% {
    left:0px;
  }
}

#SNSBar li{
  flex-grow: 4;
  list-style: none;
}
#SNSBar img{
  height:20px;
}
#SNSBar img:hover{
  margin-left: 2px;
}

#topbar{
  display: inline-flex;
  top:0px;
  -webkit-animation: topbardrop ease 3s;
          animation: topbardrop ease 3s;
  -webkit-transition: top 0.3s;
  transition: top 0.3s;
  z-index: 1;
  height: 60px;
  position: fixed;
  background-color: #F9F8F8;
  background-color: var(--white);
  width: 100%;
}

#navbar{
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-top:20px;
  margin-right: 20px;
  width:100%

}

#icon{
  margin:20px;
}


@-webkit-keyframes topbardrop{
  0% {
    top:-60px;
  }
  75%{
    top:-60px;
  }
  100% {
    top:0px;
  }
}


@keyframes topbardrop{
  0% {
    top:-60px;
  }
  75%{
    top:-60px;
  }
  100% {
    top:0px;
  }
}


.detail_btn{
  color: black;
  padding: 5px 10px 25px 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  margin: 4px 10px;
  -webkit-transition-duration: 0.4s;
          transition-duration: 0.4s;
  float:right;
  font-size: 16px;
  font-weight:600;
  padding-bottom: 0px;
  height:25px;
  font-style: italic;
}

.detail_btn:hover {
  color: #558FDC;
  color: var(--blue);
}
.small-button{
  border: 2px solid #558FDC;
  border: 2px solid var(--blue);
  border-radius: 5px;
  color: #558FDC;
  color: var(--blue);
  padding: 5px;
  padding-bottom: 3px;
}

.large-button{
  border: 2px solid #558FDC;
  border: 2px solid var(--blue);
  border-radius: 5px;
  color: #558FDC;
  color: var(--blue);
  font-size: 20px;
  padding: 10px;
  margin-left: 20px;
  text-decoration: none;
  text-align: center;
  width:30%;

}
.small-button:hover, .large-button:hover{
  background-color: #DEE8F5;
}


#home{
  padding:20%;
  margin-top: 40px;
  height: 20vh;
  display: flex;
}

#overlay_container{
  position: relative;
  margin-left: 30px;
  visibility: hidden;
}
#overlay_container.active{
  visibility: visible;
}
.image{
  display: block;
}

#overlay{
  position: absolute;
  background-color: red;
  height:100px;
  width: 100px;
  opacity: 0.5;
  -webkit-transition: .5s ease;
  transition: .5s ease;
  border-radius: 50%;

}
#overlay:hover{
  opacity:1;
}


.heading{
  -webkit-transition: all 0.8s ease;
  transition: all 0.8s ease;
  color:black;
  opacity: 0;
}
.heading.active{
  opacity: 1;
}

.fade-in {
  animation: fadeIn ease 5s;
  -webkit-animation: fadeIn ease 5s;
  -moz-animation: fadeIn ease 5s;
  -o-animation: fadeIn ease 5s;
  -ms-animation: fadeIn ease 5s;
}

#svg-header{
  position: relative;
}

.svg-fill{
  position:absolute;
  opacity: 0;
  top: 0px;
  -webkit-transition: opacity 0.5s ease, -webkit-transform 0.5s ease;
  transition: opacity 0.5s ease, -webkit-transform 0.5s ease;
  transition: opacity 0.5s ease, transform 0.5s ease ;
  transition: opacity 0.5s ease, transform 0.5s ease, -webkit-transform 0.5s ease;
}
.svg-fill.active{
  opacity: 1;
  -webkit-transform: translate3d(-3px, 3px, 0);
          transform: translate3d(-3px, 3px, 0);
}

#footer{
  font-weight: 300;
  font-size: 15px;
  font-style: italic;
  text-align: center;
  color:gray;
}

#icon{
  justify-content: left;
}
#contact{
  border: 2px solid #558FDC;
  border: 2px solid var(--blue);
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 20px;
  color: #558FDC;
  color: var(--blue);
}

#contact-left, #contact-right{
  flex: 1 1;
}
#contact-left{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}


